@import "../../../utility.scss";
.skills {
	@include respond(phone) {
		text-align: center;
	}
	.skill {
		display: inline-block;
		display: inline-block;
		background: var(--utiliy-gcolor);
		color: var(--gray-color-light2);
		padding: 0.5rem 1rem;
		border-radius: 3rem;
		margin-bottom: 0.5rem;
		cursor: pointer;
		&:not(:last-child) {
			margin-right: 2rem;
		}
	}
}
